<template>
  <fieldset class="form__fieldset">
    <div class="form__group">
      <label for="index" class="form__label">Индекс</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="index"
          placeholder="Индекс"
          v-model.trim="formAddress.index"
        />
      </div>
    </div>
    <div class="form__group">
      <label for="country" class="form__label">Страна</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="country"
          placeholder="Страна"
          v-model.trim="formAddress.country"
        />
      </div>
    </div>
    <div class="form__group">
      <label for="region" class="form__label">Область</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="region"
          placeholder="Область"
          v-model.trim="formAddress.region"
        />
      </div>
    </div>
    <div class="form__group">
      <label for="city" class="form__label">Город*</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          :class="$v.formAddress.city.$error ? 'is-invalid' : ''"
          id="city"
          placeholder="Город*"
          v-model.trim="formAddress.city"
        />
        <p
          class="input__mistake"
          v-if="$v.formAddress.city.$dirty && !$v.formAddress.city.required"
        >
          Поле обязательное для заполнения.
        </p>
      </div>
    </div>
    <div class="form__group">
      <label for="street" class="form__label">Улица</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="street"
          placeholder="Улица"
          v-model.trim="formAddress.street"
        />
      </div>
    </div>
    <div class="form__group">
      <label for="house" class="form__label">Дом</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="house"
          placeholder="Дом"
          v-model.trim="formAddress.house"
        />
      </div>
    </div>
    <button type="button" class="form__btn" @click="onBtn">Далее</button>
    <button type="button" class="form__btn form__btn-back" @click="onBtnBack">
      Назад
    </button>
  </fieldset>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  name: "AddressInfo",
  mixins: [validationMixin],
  data() {
    return {
      formAddress: {
        index: "",
        country: "",
        region: "",
        city: "",
        street: "",
        house: "",
      },
    };
  },
  methods: {
    onBtn() {
      this.$v.formAddress.$touch();
      if (!this.$v.formAddress.$error) {
        this.$emit("nextStep", this.formAddress);
      }
    },
    onBtnBack() {
      this.$emit("prevStep");
    },
  },
  validations: {
    formAddress: {
      city: {
        required,
      },
    },
  },
};
</script>

