<template>
  <fieldset class="form__fieldset">
    <div class="form__group">
      <label for="secondname" class="form__label">Фамилия*</label>
      <div class="group__input">
        <input
          type="text"
          class="form__input"
          :class="$v.formClientInfo.secondName.$error ? 'is-invalid' : ''"
          id="secondname"
          placeholder="Фамилия*"
          v-model.trim="formClientInfo.secondName"
        />
        <p
          class="input__mistake"
          v-if="
            $v.formClientInfo.secondName.$dirty &&
            !$v.formClientInfo.secondName.required
          "
        >
          Поле обязательное для заполнения.
        </p>
      </div>
    </div>
    <div class="form__group">
      <label for="firstname" class="form__label">Имя*</label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          :class="$v.formClientInfo.firstName.$error ? 'is-invalid' : ''"
          id="firstname"
          placeholder="Имя*"
          v-model.trim="formClientInfo.firstName"
        />
        <p
          class="input__mistake"
          v-if="
            $v.formClientInfo.firstName.$dirty &&
            !$v.formClientInfo.firstName.required
          "
        >
          Поле обязательное для заполнения.
        </p>
      </div>
    </div>
    <div class="form__group">
      <label for="patronymic" class="form__label">Отчество</label>
      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="patronymic"
          placeholder="Отчество"
          v-model.trim="formClientInfo.patronymic"
        />
      </div>
    </div>
    <div class="form__group">
      <label for="birthday" class="form__label">День рождения*</label>

      <div class="group__input">
        <input
          type="date"
          class="form__input"
          :class="$v.formClientInfo.birthday.$error ? 'is-invalid' : ''"
          id="birthday"
          placeholder="День рождения*"
          v-model="formClientInfo.birthday"
        />
        <p
          class="input__mistake"
          v-if="
            $v.formClientInfo.birthday.$dirty &&
            !$v.formClientInfo.birthday.required
          "
        >
          Поле обязательное для заполнения.
        </p>
      </div>
    </div>
    <div class="form__group">
      <label for="phone" class="form__label"> Номер телефона* </label>

      <div class="group__input">
        <input
          type="text"
          class="form__input"
          :class="$v.formClientInfo.phone.$error ? 'is-invalid' : ''"
          id="phone"
          maxlength="11"
          placeholder="(11 цифр. Начинается с 7)"
          @click="onPhone"
          v-model.trim="formClientInfo.phone"
        />

        <p
          class="input__mistake"
          v-if="
            $v.formClientInfo.phone.$dirty && !$v.formClientInfo.phone.required
          "
        >
          Поле обязательное для заполнения.
        </p>
        <p
          class="input__mistake"
          v-if="
            $v.formClientInfo.phone.$dirty &&
            !$v.formClientInfo.phone.numberLength &&
            $v.formClientInfo.phone.required
          "
        >
          Длина телефона должна состоять из 11 цифр.
        </p>
      </div>
    </div>
    <div class="form__group">
      <h3 class="group__title form__label">Пол</h3>
      <div class="radio__wrapper">
        <div class="radio__group">
          <label for="female" class="form__label form__label-radio">
            Женский
          </label>
          <input
            type="radio"
            class="form__radio"
            value="female"
            id="female"
            v-model="formClientInfo.gender"
          />
        </div>

        <div class="radio__group">
          <label for="male" class="form__label form__label-radio">
            Мужской
          </label>
          <input
            type="radio"
            class="form__radio"
            value="male"
            id="male"
            v-model="formClientInfo.gender"
          />
        </div>
      </div>
    </div>
    <div class="form__group">
      <label for="group-client" class="form__label">Группа клиентов*</label>
      <div class="group__input">
        <select
          class="form__input form__multiple-select"
          :class="$v.formClientInfo.groupClient.$error ? 'is-invalid' : ''"
          id="group-client"
          multiple
          v-model="formClientInfo.groupClient"
        >
          <option
            class="multiple-select__option"
            v-for="group in formClientInfo.groupClientList"
            :key="group.value"
            :value="group.value"
          >
            {{ group.label }}
          </option>
        </select>
        <p
          class="input__mistake"
          v-if="
            $v.formClientInfo.groupClient.$dirty &&
            !$v.formClientInfo.groupClient.required
          "
        >
          Поле обязательное для выбора.
        </p>
      </div>
    </div>
    <div class="form__group">
      <label for="doctor" class="form__label">Лечащий врач</label>
      <div class="group__input">
        <select
          class="form__input form__select"
          id="doctor"
          v-model="formClientInfo.doctor"
        >
          <option class="select__option" value="ivanov" selected>Иванов</option>
          <option class="select__option" value="zaharov">Захаров</option>
          <option class="select__option" value="chernysheva">Чернышева</option>
        </select>
      </div>
    </div>
    <div class="form__group form__group-agree">
      <label for="agree-sms" class="form__label form__label-checkbox"
        >Не отправлять СМС.</label
      >
      <div class="checkbox__group">
        <input type="checkbox" class="form__checkbox" id="agree-sms" />
      </div>
    </div>
    <button type="button" class="form__btn" @click="onBtn">Далее</button>
  </fieldset>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, numeric } from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],
  name: "ClientInfo",
  data() {
    return {
      formClientInfo: {
        secondName: "",
        firstName: "",
        patronymic: "",
        birthday: "",
        phone: "",
        gender: "",
        groupClientList: [
          {
            label: "VIP",
            value: "vip",
          },
          {
            label: "Проблемные",
            value: "problem",
          },
          {
            label: "ОМС",
            value: "oms",
          },
        ],
        groupClient: [],
        doctorList: [
          {
            label: "Иванов",
            value: "ivanov",
          },
          {
            label: "Захаров}",
            value: "zaharov",
          },
          {
            label: "Чернышева",
            value: "chernysheva",
          },
        ],
        doctor: "ivanov",
      },
    };
  },
  methods: {
    onPhone() {
      if (String(this.formClientInfo.phone).length < 2) {
        this.formClientInfo.phone = 7;
      }
    },
    onBtn() {
      this.$v.formClientInfo.$touch();
      const formData = {
        secondName: this.formClientInfo.secondName,
        firstName: this.formClientInfo.firstName,
        patronymic: this.formClientInfo.patronymic,
        birthday: this.formClientInfo.birthday,
        phone: this.formClientInfo.phone,
        gender: this.formClientInfo.gender,
        groupClient: this.formClientInfo.groupClient,
        doctor: this.formClientInfo.doctor,
      };
      if (!this.$v.formClientInfo.$error) {
        this.$emit("nextStep", formData);
      }
    },
  },
  watch: {
    "formClientInfo.phone"(value) {
      this.formClientInfo.phone = parseInt(value) ? parseInt(value) : 7;
      const strNumber = String(this.formClientInfo.phone);
      if (strNumber[0] != 7) {
        this.formClientInfo.phone = Number("7" + strNumber);
      }
    },
  },
  validations: {
    formClientInfo: {
      secondName: {
        required,
      },
      firstName: {
        required,
      },
      birthday: {
        required,
      },
      phone: {
        required,
        numeric,
        numberLength(value) {
          return String(value).length === 11;
        },
      },
      groupClient: { required },
    },
  },
};
</script>

