<template>
  <div id="app">
    <h1 class="register__title">Создание нового клиента</h1>
    <div class="container">
      <transition name="component-fade" mode="out-in">
        <form v-if="step < 4" class="form" action="#" @submit.prevent>
          <transition name="component-fade" mode="out-in">
            <keep-alive>
              <component
                v-bind:is="getComponentName"
                @nextStep="incrStep"
                @prevStep="dicrStep"
              ></component>
            </keep-alive>
          </transition>
        </form>
        <div v-else class="register__finish">
          <h2 class="register__subtitle">Клиент успешно добавлен!</h2>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import ClientInfo from "@/components/ClientInfo";
import AddressInfo from "@/components/AddressInfo";
import DocumentInfo from "@/components/DocumentInfo";

export default {
  name: "App",
  components: {
    ClientInfo,
    AddressInfo,
    DocumentInfo,
  },
  data() {
    return {
      step: 1,
      form: {
        formClientInfo: {},
        formAddress: {},
        formDocumentInfo: {},
      },
    };
  },
  methods: {
    onSubmit() {
      console.log("Registration finished\n", this.form);
    },
    incrStep(formData) {
      if (this.step == 1) {
        this.form.formClientInfo = formData;
      } else if (this.step == 2) {
        this.form.formAddress = formData;
      } else if (this.step == 3) {
        this.form.formDocumentInfo = formData;
      }
      this.step += 1;
      if (this.step > 3) {
        this.onSubmit();
      }
    },
    dicrStep() {
      this.step -= 1;
    },
  },
  computed: {
    getComponentName() {
      if (this.step == 1) {
        return "ClientInfo";
      } else if (this.step == 2) {
        return "AddressInfo";
      } else if (this.step == 3) {
        return "DocumentInfo";
      }
      return "ClientInfo1";
    },
  },
};
</script>

