<template>
  <fieldset class="form__fieldset">
    <div class="form__group">
      <label for="documentType" class="form__label">Тип документа*</label>

      <div class="group__input">
        <select
          class="form__input form__select"
          :class="$v.formDocumentInfo.documentType.$error ? 'is-invalid' : ''"
          id="group-client"
          v-model="formDocumentInfo.documentType"
        >
          <option
            class="multiple-select__option"
            v-for="document in formDocumentInfo.documentTypeList"
            :key="document.value"
            :value="document.value"
          >
            {{ document.label }}
          </option>
        </select>
        <p
          class="input__mistake"
          v-if="
            $v.formDocumentInfo.documentType.$dirty &&
            !$v.formDocumentInfo.documentType.required
          "
        >
          Поле обязательное для заполнения.
        </p>
      </div>
    </div>

    <div class="form__group">
      <label for="series" class="form__label">Серия</label>
      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="series"
          placeholder="Серия"
          v-model.trim="formDocumentInfo.series"
        />
      </div>
    </div>

    <div class="form__group">
      <label for="number" class="form__label">Номер</label>
      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="number"
          placeholder="Номер"
          v-model.trim="formDocumentInfo.number"
        />
      </div>
    </div>

    <div class="form__group">
      <label for="issuedByWhom" class="form__label">Кем выдан</label>
      <div class="group__input">
        <input
          type="text"
          class="form__input"
          id="issuedByWhom"
          placeholder="Кем выдан"
          v-model.trim="formDocumentInfo.issuedByWhom"
        />
      </div>
    </div>

    <div class="form__group">
      <label for="date" class="form__label">Дата выдачи*</label>
      <div class="group__input">
        <input
          type="date"
          class="form__input"
          :class="$v.formDocumentInfo.date.$error ? 'is-invalid' : ''"
          id="date"
          placeholder="Дата выдачи*"
          v-model.trim="formDocumentInfo.date"
        />
        <p
          class="input__mistake"
          v-if="
            $v.formDocumentInfo.date.$dirty &&
            !$v.formDocumentInfo.date.required
          "
        >
          Поле обязательное для заполнения.
        </p>
      </div>
    </div>
    <button type="button" class="form__btn" @click="onBtn">Далее</button>
    <button type="button" class="form__btn form__btn-back" @click="onBtnBack">
      Назад
    </button>
  </fieldset>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  name: "DocumentInfo",
  mixins: [validationMixin],
  data() {
    return {
      formDocumentInfo: {
        documentTypeList: [
          {
            label: "Паспорт",
            value: "passport",
          },
          {
            label: "Свидетельство о рождении",
            value: "birthCertificate",
          },
          {
            label: "Вод. удостоверение",
            value: "driverLicense",
          },
        ],
        documentType: "",
        series: "",
        number: "",
        issuedByWhom: "",
        date: "",
      },
    };
  },
  methods: {
    onBtn() {
      this.$v.formDocumentInfo.$touch();
      if (!this.$v.formDocumentInfo.$error) {
        this.$emit("nextStep", this.formDocumentInfo);
      }
    },
    onBtnBack() {
      this.$emit("prevStep");
    },
  },
  watch: {
    "formDocumentInfo.number"(value) {
      this.formDocumentInfo.number = parseInt(value) ? parseInt(value) : "";
    },
  },
  validations: {
    formDocumentInfo: {
      documentType: { required },
      date: { required },
    },
  },
};
</script>

<style>

</style>